.modal {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba($color: $black, $alpha: 0.5);
  backdrop-filter: blur(3px);
  z-index: 100;
}

.modal__close {
  @include button-reset;

  position: absolute;
  top: 20px;
  right: 20px;
  display: block;
  font-size: 0;
}

.modal__inner {
  position: relative;
  top: 120px;
  padding: 60px 20px 50px;
  min-height: 400px;
  background-color: $bg-main;
}

.modal__title {
  margin: 0;
  margin-bottom: 35px;
  padding: 0 10px;
  font-weight: normal;
  font-size: 20px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.09em;
  text-transform: uppercase;
}
