.main-nav__toggler {
  display: block;
  border: 0;
  margin: 0;
  padding: 0;
  background-color: transparent;
  position: absolute;
  right: 10%;
  top: 28px;
  width: 37px;
  height: 11px;
  font-size: 0; }
  .main-nav__toggler span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #ffffff;
    transition: all 0.3s ease-in; }
    .main-nav__toggler span:last-child {
